<template>
    <div>
        <h1 class="divided">海钓</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <template v-if="result.time">
                <div>
                    <h3>海钓时间：{{result.time.start}} ~ {{result.time.end}}</h3>
                    <h3>海钓地点：{{result.time.map}}</h3>
                    <h3>排名奖励</h3>
                        <table border="1" class="labryinth-table">
                            <thead>
                                <th>排名</th>
                                <th>奖励</th>
                            </thead>
                            <tr v-for="(v,k) in result.reward" :key="k">
                                <td v-if="v._StartRankReward == v._EndRankReward">第{{v._StartRankReward}}名</td>
                                <td v-else>第{{v._StartRankReward}} ~ {{v._EndRankReward}}名</td>
                                <td>
                                    <div class="Title">
                                        <div v-for="(vv,kk) in v.reward" :key="kk" class="titleitem">
                                            <item-icon-tooltip
                                                    :itemId="vv.item.id" 
                                                    :itemStub="vv.item"
                                                    :count="vv.count"
                                                    :noLink="noLink">
                                            </item-icon-tooltip>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                    </table>
                </div>

                <h3>海钓掉落</h3>
                <item-drop-list :dropId="167772162" :dropsItem="result.drops"></item-drop-list>
            </template>
            <template v-else>
                <h3>今天无海钓活动</h3>
            </template>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
import ItemCard from "@/old/items/ItemCard";
Vue.component('item-card', ItemCard);
import ItemDropList from "@/components/item/ItemDropList.vue";

export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        ItemDropList
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.FishingCache){
                this.result = GameInfo.FishingCache;
                this.loading = false;
            }else{
            GameInfo.Fishing(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
</style>